html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;

}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  min-width: 300px;
  width: 100%;
  /* flex-basis: 300px; */
  /* flex-grow: 1; */
  /* text-align: center; */
  color: white;
}

.block--1 {
  background-color: rgb(100%, 0%, 0%);
}

.block--2 {
  background-color: rgb(80%, 0%, 0%);
}

.block--3 {
  background-color: rgb(60%, 0%, 0%);
}

.block--4 {
  background-color: rgb(40%, 0%, 0%);
}

.block--5 {
  background-color: rgb(20%, 0%, 0%);
}

.block--6 {
  background-color: rgb(0%, 0%, 0%);
}

@media screen and (min-width:600px ) {
  .block {
    flex-basis: 50%;
  }
  .block--3 {
    order: 2;
  }
  .block--4 {
    order: 1;
  }

  .block--5 {
    order: 3;
  }
  .block--6 {
    order: 4;
  }
}

@media screen and (min-width:900px) {
  .block {
    flex-basis: calc(100% / 3);
  }
  .block--1,
  .block--2,
  .block--3 {
    order: 0;
  }
  .block--4 {
    order: 3;
  }
  .block--5 {
    order: 2;
  }
  .block--6 {
    order: 1;
  }

}
